.page {
  scroll-behavior: smooth;
  font-family: Manrope, Arial, sans-serif;
  color: $main-text-color;

  &__body {
    min-width: 320px;
    margin: 0;
  }

  &__menu {
    pointer-events: none;

    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transform: translateX(100%);

    opacity: 0;

    transition: all 0.3s;

    &:target {
      pointer-events: all;
      transform: translateX(0);
      opacity: 1;
    }
  }
}
